import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { selectVoteOptionState, updateVote, loadVoteState } from "./voterSlice";
import { WebSocketManager } from "../../WebSocketManager";
import { VoteOptionStateModel } from "../../ChaosApiClient";
import VoteButton from "./VoteButton";

let WSM: WebSocketManager | undefined = undefined;

export function Voter() {
    const voteOptionState = useSelector(selectVoteOptionState);

    const dispatch = useDispatch();

    const handleIncomingMessage = (ev: MessageEvent) => {
        let data = VoteOptionStateModel.fromJS(JSON.parse(ev.data));
        dispatch(updateVote(data));
    };

    useEffect(() => {
        WSM = new WebSocketManager(handleIncomingMessage);
        dispatch(loadVoteState());
    }, []);

    return (
        <div className="container">
            <h1 className="text-center">
                GTA V Torture Tool{" "}
                <small className="text-muted">for use with 'friends'</small>
            </h1>
            <p className="lead text-center">
                Previous vote: {voteOptionState.previousVotingResult}
            </p>

            {!voteOptionState.voteOptionsState && (
                <div className="spinner-border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </div>
            )}

            {voteOptionState.voteOptionsState && (
                <div className="container">
                    <div className="row row-cols-2 gy-2">
                        {voteOptionState.voteOptionsState.map((voteOption) => {
                            return <VoteButton voteOption={voteOption} />;
                        })}
                    </div>
                </div>
            )}
        </div>
    );
}
