import { createSlice, Dispatch, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../app/store";
import { ChaosApiClient, VoteOptionStateModel } from "../../ChaosApiClient";
import { apiUrl } from "../../appConfig";

let Api = new ChaosApiClient(apiUrl);

interface VoterState {
    voteOptionState: VoteOptionStateModel;
}

export const initialState: VoterState = {
    voteOptionState: new VoteOptionStateModel({
        previousVotingResult: "",
        voteOptionsState: [],
    })
};

export const voterSlice = createSlice({
    name: "voter",
    initialState,
    reducers: {
        updateVote: (state, action: PayloadAction<VoteOptionStateModel>) => {
            state.voteOptionState = action.payload;
        }
    },
});

export const { updateVote } = voterSlice.actions;

export const loadVoteState = () => {
    return async (dispatch: Dispatch) => {
        try {
            await Api.getCurrentVoteState().then((result) => {
                return dispatch(updateVote(result));
            });
        } catch (e) {
            console.log(e);
        }
    };
};

export const vote = (optionId: string) => {
    return async (dispatch: Dispatch) => {
        try {
            await Api.vote(optionId);
        } catch (e) {
            console.log(e);
        }
    };
};

export const selectVoteOptionState = (state: RootState) =>
    state.voter.voteOptionState;

export default voterSlice.reducer;
