import { socketUrl } from "./appConfig";

export class WebSocketManager {
    Socket: WebSocket | undefined;
    HandlerFunc: (ev: MessageEvent) => any;

    constructor(handler: (ev: MessageEvent) => any) {
        this.HandlerFunc = handler;
        this.setupSocket();
    }

    setupSocket = () => {
        try {
            this.Socket = new WebSocket(socketUrl);
        } catch (exc: any) {
            console.log(
                `An error occurred while setting up the socket, retrying in 5s: ${exc}`
            );
            setTimeout(() => this.setupSocket(), 5000);
            return;
        }

        this.Socket.onopen = this.onOpen;
        this.Socket.onclose = this.onClose;
    };

    onOpen = (ev: Event) => {
        console.log('Socket connection established');
        this.Socket!.onmessage = this.HandlerFunc;
    };

    onClose = (ev: CloseEvent) => {
        console.log("Web Socket was closed, attempting to reconnect in 5s");

        setTimeout(() => this.setupSocket(), 5000);
    };
}
