import { VoteOptionModel } from "../../ChaosApiClient";
import { useDispatch } from "react-redux";
import { vote } from "./voterSlice";

interface VoteButtonProps {
    voteOption: VoteOptionModel;
}

const VoteButton = ({ voteOption }: VoteButtonProps) => {
    const dispatch = useDispatch();

    return (
        <div className="col d-grid gap-1" key={voteOption.optionId!}>
            <button
                className="btn btn-primary"
                onClick={() => {
                    dispatch(vote(voteOption.optionId!));
                }}
            >
                <span>{voteOption.optionDescription}</span>
                <br />
                <span>Votes: {voteOption.optionVotes}</span>
            </button>
        </div>
    );
}

export default VoteButton;